.search{
  display:flex;
  flex-wrap:wrap;
  justify-content: space-between;
  padding:2.3em 5em;
}
input,select{
  background-color: hsl(209, 23%, 22%);
  color:white;
  border:none;
  border-radius:3px;
  font-size:0.875rem;
  outline: none;
}
input{
  padding:1.3em;
  width:35%;
}
::placeholder{
  color:white;
}
select{
  padding:0.8em;
}
option{
  margin-top: 2em;
}
@media(max-width:560px){
  .search{
    display:inline-block;
    margin:auto;
    padding: 0.5em 2em;
  }
  input{
    padding:0.8em;
    width:100%;
    margin-bottom:10px;
  }
  select{
    padding:0.5em;
    width:100%;
  }
}