
.card{
  background-color:hsl(209, 23%, 22%);
  font-size:14px;
  line-height:2em;
  border-radius:8px;
  margin:0;
  transition: linear 2ms;
  min-height:30em;
}
.card:hover{
  transform: scale(1.05);
  cursor: pointer;
}
.card-body{
  color:white;
  padding:1em 1em 3em 1.5em;


}
.country-flag{
  border-radius:8px 8px 0 0;
}
img{
  width:100%;

}
@media(max-width:560px){
  .card{
margin-top:2em;
  }
}
