.country-details{
  padding:0.5em 5em;
}
.back-button{
  padding:0.8em 3em;
  margin:4em 0;
  border:none;
  border-radius:3px;
  background-color:#2B3743;
  font-size:0.875em;
  box-shadow:1px 1px 1px  #1A2730;
  color:white;


}
img{
  border:none;
}
.border-list{
  display:grid;
  grid-template-columns: repeat(3,1fr);
}
.border-button{
  margin:0.3em;
  padding:0.8em 1.5em;
  border:none;
  border-radius:3px;
  background-color:#2B3743;
  font-size:0.875em;
  box-shadow:1px 1px 1px  #1A2730;
  color:white;

}
.link{
  text-decoration: none;
}
.country-info{
  display:grid;
  grid-template-columns: repeat(2,1fr);
  grid-gap:2em;
  color:white;
}
.country-info h1{
  margin-bottom:1.5em;
}
.other-details{
  display:grid;
  grid-template-columns: repeat(2,1fr);
  grid-gap:0.1em;
  line-height:1.9em;
}
.languages{
  display:grid;
  grid-template-columns: repeat(3,1fr);
  grid-gap:2px;
}


@media(max-width:560px){
  .country-details{
    padding:0.5em 1em;
  }
  .country-info{
    display:block;
    margin: auto;
    width:80%;
    color:white;
  }
  .other-details{
    display:block;
    line-height:2em;

  }
  .country-name{
    text-align: center;
  }
}