body {
  margin: 0;
  background-color: hsl(207, 26%, 17%);
  font-family: 'Montserrat', 'Nunito Sans';
}

.country {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2em;
  padding: 0.5em 5em;
}

@media(max-width:560px) {
  .country {
    display: block;
  padding: 0.5em 2em;

  }
}